bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.profly", {}, {
    /*
    *   Constructor
    */
    init: function (workportalFacade, dataService, params) {
        var self = this;

        // Call base
        self._super(workportalFacade, dataService, params);

        //Load templates
        self.loadTemplates({
            "project-filespreview-wrapper": bizagi.getTemplate("bizagi.workportal.desktop.widget.profly").concat("#project-filespreview-wrapper")
        });
    },

    /**
    * Renders the template defined in the widget
    */
    renderContent: function () {
        var self = this;
        // Define parameters to route angular app
        var argumentsTemplate = {
            paramsURL: ''
        }
        if(self.params.data && self.params.data.route && self.params.data.params.id){
            argumentsTemplate.paramsURL =
                encodeURI('?route=' + self.params.data.route + '&id=' + self.params.data.params.id + '&displayName=' + self.params.data.params.displayName);

        }

        if(self.params.data && self.params.data.route && self.params.data.params.module){
            argumentsTemplate.paramsURL =
                encodeURI('?route=' + self.params.data.route + '&module=' + self.params.data.params.module);
        }

        var generalTmpl = self.getTemplate("project-filespreview-wrapper");
        self.content = generalTmpl.render(argumentsTemplate);

        if(bizagi.util.isIE11OrLower()){
            localStorage.setItem("params_profly", argumentsTemplate.paramsURL);
            $(self.content).css({ width: '0px', height: '0px' });
            $.when(self.reloadToDashboardWhenLoadIFrame()).done(function(){
                $(self.content).css('width', '100%').css('height', '100%');
            });
        }

        // this event is triggered when user lost session from form designer.
        // by the moment, only Chrome Users can access to formDesigner.
        if(bizagi.util.isChrome()){
            $(self.content).on("load", function (event) {
                if(event.currentTarget.contentWindow.location.href.indexOf('quickprocess') === -1){
                    location.reload(); // QAF-5934
                }
            });
        }

        return self.content;
    },

    reloadToDashboardWhenLoadIFrame: function () {
        var self = this;
        var defer = $.Deferred();
        function waitForContentDocumentOnIframe(){
            if(document.getElementById('iframe-liveprocesses') && typeof document.getElementById('iframe-liveprocesses').contentDocument !== "undefined"){
                document.getElementById('iframe-liveprocesses').contentDocument.location = './jquery/quickprocess/dist/';
                setTimeout(function(){defer.resolve();}, 1000);
            }
            else{
                setTimeout(waitForContentDocumentOnIframe, 50);
            }
        }

        waitForContentDocumentOnIframe();
        return defer.promise();
    },

    /**
    * links events with handlers
    */
    postRender: function () {
        var self = this;
    },

    clean: function () {
        var self = this;
        $('#iframe-liveprocesses', self.content).off();
        self.plugins = {};
        self.form = {};
        self.currentFile = {};
        self.filesNumber = 0;
    }
});

bizagi.injector.register("bizagi.workportal.widgets.profly", ["workportalFacade", "dataService", bizagi.workportal.widgets.profly]);